@media (max-width:1024px) {
.new-collections{
    gap: 15px;
}
    .new-collections h1 {
        font-size: 30px;
    }

    .new-collections hr {
        width: 120px;
        height: 5px;
    }

    .collections {
        gap: 15px;
        margin-top: 30px;
    }
}

@media (max-width:800px) {
  

    .new-collections h1 {
        font-size: 20px;
    }

    .new-collections hr {
        width: 120px;
        height: 3px;
    }

    .collections{
        gap: 15px;

    }
}

@media (max-width:600px) {
  

   

    .collections{
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;

    }
}