 @media (max-width:800px) {
     .newsletter {
         margin-bottom: 30px;
         height: 35vh;
         gap: 20px;
     }

     .newsletter h1 {
         font-size: 18px;

     }

     .newsletter p {
         font-size: 13px;

     }

     .newsletter div {
         width: 400px;
         height: 40px;
     }

     

     .newsletter input {
         width: 200px;

         font-size: 13px;
     }

     .newsletter div button {
         width: 100px;
         height: 40px;
         font-size: 13px;
     }
    }
 @media (max-width:600px) {
     .newsletter {
        width: 100%;
         height: 25vh;
         padding: 0;
         gap: 20px;
     }

     .newsletter h1 {
         font-size: 16px;

     }

     .newsletter p {
         font-size: 13px;
         min-width: 200px;
         text-align: center;

     }

     .newsletter div {
         width: 290px;
     }

     

     .newsletter input {
         width: 130px;

     }

    
    }