 @media(max-width:1024px){
    .hero-left {
            padding-left: 60px;
        }
    
        .hero-left h2 {
            font-size: 18px;
        }
    
        .hero-left p {
            font-size: 44px;
        }
    
        .hero-hand-icon img {
            width: 55px;
        }
    
        .hero-latest-btn {
            gap: 10px;
            width: 200px;
            height: 45px;
            font-size: 14px;
            margin-top: 10px;
        }
    
        .hero-right img {
            width: 450px;
        }
 }
 @media (max-width:800px) {
    .hero-left {
            padding-left: 30px;
        }
    
        .hero-left h2 {
            font-size: 18px;
        }
    
        .hero-left p {
            font-size: 36px;
        }
    
        .hero-hand-icon img {
            width: 45px;
        }
    
        .hero-latest-btn {
            gap: 10px;
            width: 170px;
            height: 40px;
            font-size: 13px;
            margin-top: 10px;
        }
    
        .hero-right img {
            width: 300px;
        }
 }
 @media (max-width:575px) {
    .hero{
        flex-direction: column;
        padding-top: 200px;
        padding-left: 80px;
        height: 50vh;
    }
    .hero-left{
        justify-content: center;
    }
         .hero-right img {
             display: none;
         }
         .hero-left h2{
            font-size: 16px;
         }
         .hero-left p{
            font-size: 50px;
         }
         .hero-hand-icon img{
            width: 50px;
         }
         .hero-latest-btn{
            width: 200px;
            height: 45px;
            font-size: 14px;
         }
 }