@media(max-width:1024px){
    .offers{
        padding: 0px 60px;
        margin-bottom: 60px;
        height: 50vh;
    }
    .offers-left h1{
font-size: 30px;
    }
    .offers-left p{
        font-size: 15px;
    }
    .offers-right img{
        width:170px;
    }
    .offers-left button{
        width: 160px;
        height: 40px;
        font-size: 16px
        ;
    }
    .offers-right{
        padding-top: 10px;
    }
}
@media(max-width:800px){
    .offers{
        margin-bottom: 40px;
    }
    .offers-left h1{
font-size: 24px;
    }
    .offers-left p{
        font-size: 13px;
    }
    .offers-right img{
        width:170px;
    }
    .offers-left button{
        width: 140px;
        height: 30px;
        font-size: 14px
        ;
    }
    
}
@media(max-width:575px){
    .offers{
        height: 30vh;
        margin-bottom: 50px;
    }
    .offers-left h1{
font-size: 16px;
    }
    .offers-left p{
        font-size: 10px;
    }
    .offers-right img{
        width:120px;
    }
    .offers-left button{
        width: 100px;
        height: 25px;
        font-size: 10px
        ;
        margin-top: 10px;
    }
    
}