@media (max-width:1024px) {
    .popular{
        height: 65vh;
    }
    .popular h1{
        font-size: 30px;
    }
    .popular hr{
         width: 120px;
         height: 5px;
    }
    .popular-item{
        gap: 15px;
        margin-top: 30px;
    }
} 
@media (max-width:800px) {
    .popular{
        height: 50vh;
        gap: 6px;
    }
    .popular h1{
        font-size: 20px;
    }
    .popular hr{
         width: 120px;
         height: 3px;
    }
    .popular-item{
        gap: 15px;
       
} 
}
@media (max-width:600px) {
    .popular{
        height: 105vh;
    }
    .popular h1{
        font-size: 20px;
    }
    .popular hr{
         width: 120px;
         height: 3px;
    }
    .popular-item{
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
       
} 
}