@media (max-width:1024px) {
    .relatedproducts {
        height: 65vh;
    }

    .relatedproducts h1 {
        font-size: 30px;
    }

    .relatedproducts hr {
        width: 120px;
        height: 5px;
    }

    .relatedproducts-item {
        gap: 15px;
        margin-top: 30px;
    }
}

@media (max-width:800px) {
    .relatedproducts {
        height: 70vh;
        gap: 6px;
    }

    .relatedproducts h1 {
        font-size: 20px;
    }

    .relatedproducts hr {
        width: 120px;
        height: 3px;
    }

    .relatedproducts-item {
        gap: 15px;

    }
}

@media (max-width:600px) {
    .relatedproducts {
        height: 125vh;
    }

    .relatedproducts h1 {
        font-size: 20px;
    }

    .relatedproducts hr {
        width: 120px;
        height: 3px;
    }

    .relatedproducts-item {
      display: grid
      ;
      grid-template-columns: 1fr 1fr;
    gap: 30px;
    }
}