@media (max-width:1024px) {
    .item{
        width: 180px;
        font-size: 13px;
    }
    .item img{
        width: 100%;
    }
    .item-price-old{
        font-size: 13px;
    }
}
@media(max-width:800px) {
    .item{
        width: 120px;
        font-size: 12px;
    }
    .item img{
width :100%
    }
    .item-price-old{
        font-size: 12px;
    }
}
@media(max-width:600px){
    .item{width: 160px;}
    .item img{
        width: 100%;
    }
}